.image25Icon {
  position: relative;
  width: 25px;
  height: 25px;
  object-fit: cover;
}
.image25Wrapper {
  display: flex;
  flex-direction: row;
  padding: var(--padding-mini) var(--padding-3xs) 0;
  align-items: center;
  justify-content: center;
}
.home {
  position: relative;
  font-weight: 500;
}
.frameGroup,
.homeWrapper {
  display: flex;
  flex-direction: row;
  padding: 0 var(--padding-3xs);
  align-items: flex-start;
  justify-content: flex-start;
}
.frameGroup {
  background-color: var(--color-gray-100);
  width: 120px;
  height: 75px;
  flex-direction: column;
  padding: var(--padding-3xs);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  gap: var(--gap-3xs);
}
.image27Icon {
  position: relative;
  width: 25px;
  height: 26.92px;
  object-fit: cover;
}
.frameDiv {
  background-color: var(--color-gainsboro-100);
  width: 120px;
  height: 75px;
  display: flex;
  flex-direction: column;
  padding: var(--padding-3xs);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  gap: var(--gap-3xs);
}
.frameParent {
  display: flex;
  flex-direction: row;
  padding: var(--padding-11xl) 0 0;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: center;
  font-size: var(--font-size-xs);
  color: var(--color-black);
  font-family: var(--font-inter);
}
